@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;600&display=swap");
:root {
  --Header_fonts: "Poppins", sans-serif;
  --accent-fonts-lighter: "Poppins", sans-serif;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  font-family: var(--accent-fonts-lighter);
}
.Navbar {
  width: 100%;
  height: 70px;
  background: #ffffff;
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
  position: relative;
}
.Navbar img {
  position: absolute;
  width: 82.02px;
  height: 25.35px;
  left: 30px;
  top: 22px;
}
.Hero {
  height: auto;
  padding: 4rem;
  padding-bottom: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Hero .hero-img-container {
  width: 60%;
  height: auto;
}
.hero-img-container img {
  width: 100%;
  height: auto;
  object-fit: contain;
}
.Content {
  text-align: center;
  margin-bottom: 2rem;
  margin-top: 2rem;
}
.Content h1 {
  font-family: var(--Header_fonts);
  font-style: normal;
  font-weight: 900;
  font-size: 36px;
  color: #000000;
}
.Content p {
  font-family: var(--accent-fonts-lighter);
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  color: #222222;
  margin-top: 16px;
}
.App .Card-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;

  gap: 2rem;
  margin-top: 1rem;
  padding: 2rem;
}
.App .Card-container > * {
  flex-basis: 100%;
  padding: 2rem;
  position: relative;
  cursor: pointer;
}
.App .Card-container .card-badge {
  position: absolute;
  left: 43px;
  top: 50px;
  background: #ffffff;
  border-radius: 4px;
  padding: 0.3125rem 1rem;
  font-weight: bold;
}
.App .Card-container img {
  width: 100%;
}
.card-body {
  padding: 1rem 0rem;
}
.fa-star {
  color: red;
}
.card-body .card-text {
  margin-top: 1rem;
}
@media screen and (max-width: 40rem) {
  .Hero {
    height: auto;
    padding: 2rem;
  }
  .Hero .hero-img-container {
    width: 100%;
    height: auto;
  }
  .Hero img {
    width: 100%;
    height: auto;
  }
  .Content {
    padding: 1rem;
    padding-bottom: 0rem;
    margin-top: 0rem;
    margin-bottom: 0rem;
    line-height: 1.4rem;
    text-align: left;
  }
  .Content h1 {
    font-size: 2.25rem;
    margin-left: 1rem;
  }
  .Content p {
    font-size: 1rem;
    margin-left: 1rem;
  }
  .App .Card-container {
    flex-wrap: wrap;
    padding: 1rem;
  }
  .App .Card-container img {
    width: 100%;
  }
  .App .Card-container > * {
    padding: 1rem;
  }
}
